
import XHeader from 'vux/src/components/x-header/index.vue'
import XCircle from 'vux/src/components/x-circle/index.vue'


export default {
name: 'OrderDetail',
components: {
XHeader,
XCircle
},
created() {
this.getData();
},
data() {
return {
info: {},
data1: true,
data2: true,
data3: true,
data4: false
};
},
methods: {
async getData() {
this.$vux.loading.show({
text: 'Loading'
});
let res = await this.$http('/queryBatchOrderDetail', {
body: {
batchOrderId: this.$route.params.id
}
});
this.info = Object.assign({}, res.returnMap.batchOrderInfo);
if(this.info.isTrackOrder==="1"){
this.data4=true;
}

if(this.info.isJoinOrder==="1"){
this.data3=true;
}
this.$vux.loading.hide();
}
}
};



